function isWalletAvailable() {
  if (typeof window.conflux !== 'undefined') {
    return conflux.isConfluxPortal;
  }
  return false;
}

async function isConnectedToNetwork() {
  try {
    const check = await mountainContract.tokenToCoin('1000000000000000000');
    return true;
  } catch (err) {
  }
  return false;
}

