async function getWalletData() {
  let gasBalance = await cfx.getBalance(account);
  gasBalance = ethers.utils.formatEther(gasBalance.toString());
  let cusdtBalance = await cusdtContract.balanceOf(account);
  cusdtBalance = ethers.utils.formatEther(cusdtBalance.toString());
  let tokenBalance = await tokenContract.balanceOf(account);
  tokenBalance = ethers.utils.formatEther(tokenBalance.toString());
  return {
    address: account,
    usdt: Math.floor(cusdtBalance * 100) / 100,
    gas: Math.floor(gasBalance * 100) / 100,
    token: Math.floor(tokenBalance * 100) / 100,
  };
}

const walletDataSubscribers = [];

function subscribeWalletData(cb) {
  walletDataSubscribers.push(cb);
}

async function publishWalletData() {
  const data = await getWalletData();
  for (let i = 0; i < walletDataSubscribers.length; i++) {
    const cb = walletDataSubscribers[i];
    cb(data);
  }
}

async function approveMint(amount) {
  const approveAmount = ethers.utils.parseUnits(amount + '');
  const tx = await cusdtContract.approve(WARRIOR_ADDRESS, approveAmount.toString()).sendTransaction({ from: account }).executed();
  const txHash = tx.transactionHash;
  return txHash;
}

async function getAllowanceMint() {
  let allowance = await cusdtContract.allowance(account, WARRIOR_ADDRESS);
  allowance = ethers.utils.formatEther(allowance.toString());
  return allowance.toString();
}

async function mint(amount) {
  const amountCoin = ethers.utils.parseUnits(amount + '');
  const tx = await warriorContract.summon(amountCoin).sendTransaction({ from: account }).executed();
  const txHash = tx.transactionHash;
  // const receipt = await cfx.getTransactionReceipt(txHash);
  // console.log(receipt.logs);
  return txHash;
}

function openExplorerTx(txHash) {
  Lib.openUrl('https://testnet.confluxscan.io/transaction/' + txHash);
}

function openExplorerNft() {
  const url = 'https://testnet.confluxscan.io/nft-checker/' + account;
  Lib.openUrl(url);
}

async function getFloorPrice() {
  let price = await mountainContract.tokenToCoin('1000000000000000000');
  price = ethers.utils.formatEther(price.toString());
  return price;
}

async function tokenToCoin(amount) {
  if (Number(amount) > 0) {
    const amountToken = ethers.utils.parseUnits(amount + '');
    let price = await mountainContract.tokenToCoin(amountToken);
    price = ethers.utils.formatEther(price.toString());
    return price;
  } else {
    return 0;
  }
}

async function approveSell(amount) {
  const approveAmount = ethers.utils.parseUnits(amount + '');
  const tx = await tokenContract.approve(MOUNTAIN_ADDRESS, approveAmount.toString()).sendTransaction({ from: account }).executed();
  const txHash = tx.transactionHash;
  return txHash;
}

async function getAllowanceSell() {
  let allowance = await tokenContract.allowance(account, MOUNTAIN_ADDRESS);
  allowance = ethers.utils.formatEther(allowance.toString());
  return allowance.toString();
}

async function sell(amount) {
  const amountToken = ethers.utils.parseUnits(amount + '');
  const tx = await mountainContract.buyBack(amountToken).sendTransaction({ from: account }).executed();
  const txHash = tx.transactionHash;
  return txHash;
}

async function getOwnedNFT() {
  let num = await warriorContract.balanceOf(account);
  num = Number(num.toString());
  const rows = [];
  for (let i = 0; i < num; i++) {
    let nftId = await warriorContract.tokenOfOwnerByIndex(account, i);
    nftId = Number(nftId.toString());
    let stat = await warContract.getNftStat(nftId);
    const power = Number(stat[2].toString());
    stat = await warriorContract.getStat(nftId);
    let collateral = Number(ethers.utils.formatEther(stat[1].toString()));
    const ts = Number(stat[4].toString());
    const rarity = Number(stat[5].toString());
    let earning = await warContract.getTotalReward(nftId, 0);
    earning = earning[0];
    earning = Number(ethers.utils.formatEther(earning.toString()));
    rows.push({ nftId: nftId, power: power, earning: earning, noPenaltyTime: ts, rarity, collateral });
  }

  return rows;
}

async function getNextSessionTime() {
  let stat = await warContract.getStat();
  let ts = Number(stat[4].toString());
  return ts;
}

async function retrieveReward(nftId) {
  const tx = await warContract.withdrawReward(nftId, 0).sendTransaction({ from: account }).executed();
  const txHash = tx.transactionHash;
  return txHash;
}

async function burn(nftId) {
  const tx = await warriorContract.unsummon(nftId).sendTransaction({ from: account }).executed();
  const txHash = tx.transactionHash;
  return txHash;
}


export {
  isWalletAvailable,
  isConnectedToNetwork,
  connectToWallet,
  getWalletData,
  subscribeWalletData,
  publishWalletData,
  getAllowanceMint,
  approveMint,
  mint,
  openExplorerTx,
  openExplorerNft,
  tokenToCoin,
  getFloorPrice,
  getAllowanceSell,
  approveSell,
  sell,
  getOwnedNFT,
  getNextSessionTime,
  retrieveReward,
  burn
};


