import React from 'react';
import { View, Dimensions, Text, Image, ScrollView, ActivityIndicator } from 'react-native';
import { CheckBox, Button, Icon } from 'react-native-elements';
import { isConnectedToNetwork } from '../BlockLib';
import Lib from '../Lib';

const { showToast, getStorageValue, setStorageValue } = Lib;
const { IMG_DATA, USDT, TOKEN, TOKEN_NAME } = require('../Data');

class PanelMenu extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: 'busy'
    };
  }

  async componentDidMount() {
    this.setState({ show: 'game' });
  }

  componentWillMount() {
  }

  async handleShow(show) {
    const isConnected = await isConnectedToNetwork();
    if (!isConnected) {
      return showToast('NOT CONNECTED TO NETWORK');
    }
    this.props.handleShow(show)
  }

  render() {
    const ss = { flex: 1, justifyContent: 'center', paddingVertical: 10 };
    return (
      <View style={{ padding: 5, backgroundColor: '#eee' }}>
        <View style={{ flexDirection: 'row', alignItems: 'stretch' }}>
          <View style={{ flex: 1, padding: 5 }}>
            <Icon name='collections' size={40} />
            <View style={ss}><Text style={{ textAlign: 'center' }}>OWNED WARRIORS</Text></View>
            <Button title="MANAGE" onPress={() => this.handleShow('MANAGE')} />
          </View>
          <View style={{ flex: 1, padding: 5 }}>
            <Icon name='account-circle' size={40} />
            <View style={ss}><Text style={{ textAlign: 'center' }}>NEW WARRIOR</Text></View>
            <Button title="MINT" onPress={() => this.handleShow('MINT')} />
          </View>
          <View style={{ flex: 1, padding: 5 }}>
            <Icon name='account-balance-wallet' size={40} />
            <View style={ss}><Text style={{ textAlign: 'center' }}>{TOKEN_NAME.toUpperCase()}</Text></View>
            <Button title="SWAP" onPress={() => this.handleShow('SELL')} />
          </View>
        </View>
      </View>
    );
  }

}

export default PanelMenu;
